import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Tabbar/Home.vue'
import Tabbar from './views/Tabbar.vue'
import Directory from './views/Tabbar/Directory.vue'


Vue.use(Router)

export default new Router({
  routes: [{
      path: '/tabbar',
      name: 'tabbar',
      component: Tabbar,
      children: [{
        path: 'home',
        name: 'home',
        component: Home
      }]
    }, {
      path: '/directory',
      name: 'directory',
      component: Directory
    },
    {
      //
      path: '/',
      redirect: () => {
        //   
        // return  /
        return '/tabbar/home'
      }
    }
  ]
})